<InfoRow>
    <span slot="left">
        {#if differentStartTime}
            <span class="differentTime">{departureTimeString}</span>
        {/if}

        <span class="{differentStartTime && "originalTimeDifferent"}">
            {startTimeString}
        </span>
    </span>
    <span slot="center">
        <HeadSign icon="bus" {backgroundColor} {textColor}>{line}</HeadSign>
    </span>
    <span slot="right">
        <span class="{differentEndTime && "originalTimeDifferent"}">
            {endTimeString}
        </span>

        {#if differentEndTime}
            <span class="differentTime">{arrivalTimeString}</span>
        {/if}
    </span>
</InfoRow>

<InfoRow>
    <span slot="left"><span class="mdi mdi-arrow-expand-right"></span> {step.fromBusStop.name}</span>

    <span slot="right">
        {#if departurePass && departurePass.platform}
            <span class="platform">Platform {departurePass.platform}</span>
        {/if}
    </span>
</InfoRow>
<InfoRow>
    <span slot="left"><span class="mdi mdi-arrow-collapse-right"></span> {step.toBusStop.name}</span>

    <span slot="right">
        {#if arrivalPass && arrivalPass.platform}
            <span class="platform">Platform {arrivalPass.platform}</span>
        {/if}
    </span>
</InfoRow>

{#if currentPassLoaded}
    <InfoRow>
        <span slot="left">
            <span class="mdi mdi-rss"></span>
            <span class="delta">{deltaString}</span>

            {#if currentPass.status === "arrived"}
                Aangekomen bij 
            {:else if currentPass.status === "driving"}
                Rijd naar 
            {:else if currentPass.status === "passed"}
                Bus is gepasseerd
            {:else if currentPass.status === "planned"}
                Geplanned
            {:else if currentPass.status === "cancelled"}
                Stopt niet bij
            {/if}
        
            {currentPass.name}
        </span>
    </InfoRow>
{/if}


{#if journeyLoaded}
    <MoreInfo>
        <InfoRow>
            <span slot="left">
                <span class="mdi mdi-routes"></span>
                Richting
            </span>
            <span slot="right">
                {journey.line.destination.name}
            </span>
        </InfoRow>

        {#if currentPass}
            <BusTimeLine {stops} currentStop={currentPass} />
        {/if}
    </MoreInfo>
{/if}

<script>
import { onMount, onDestroy } from "svelte";
import { getBusStop, findDeparture, getBusStopsFromJourney, getBusTrip, getActiveStops, getCurrentStop } from "@/js/busUtils.js";
import { getStepColor, getStepTextColor, createStepStore } from "@/js/stepUtils.js";
import { timestampToString } from "@/js/utils/timeUtils.js";
import { createPoller } from "@/js/controllers/liveData.js";
import getDirectDistance from "@/js/utils/directDistanceUtils.js";

// Components
import InfoRow from "@/components/Misc/InfoRow.svelte";
import HeadSign from "@/components/HeadSign.svelte";
import MoreInfo from "@/components/MoreInfo.svelte";
import BusTimeLine from "@/components/RouteStep/BusTimeLine.svelte";

// Props
export var step;

// Data
var journey = null;
var currentPass = null;

var journeyPoller = null;
var locationPoller = null;

// Computed
$: journeyLoaded = journey !== null;
$: currentPassLoaded = currentPass !== null;
$: line = step.lineNumber;
$: backgroundColor = getStepColor(step);
$: textColor = getStepTextColor(step);
$: startTimeString = timestampToString(step.departureTime.timestamp);
$: endTimeString = timestampToString(step.arrivalTime.timestamp);
$: stops = journeyLoaded ? journey.passes : [];
$: delta = currentPassLoaded ? currentPass.arrivalDelta.milliseconds : 0;
$: deltaString = toDeltaString(delta);
$: departurePass = matchPass(journey, step.fromLocation);
$: arrivalPass = matchPass(journey, step.toLocation);
$: departureTimeString = departurePass && timestampToString(departurePass.expectedArrivalTime.timestamp);
$: arrivalTimeString = arrivalPass && timestampToString(arrivalPass.expectedArrivalTime.timestamp);
$: differentStartTime = departureTimeString ? departureTimeString !== startTimeString : false;
$: differentEndTime = arrivalTimeString ? arrivalTimeString !== endTimeString : false;

// Functions
function toDeltaString(milliseconds) {
    let leadingCharacter = "";
    if (milliseconds < 0) {
        milliseconds = Math.abs(milliseconds);
        leadingCharacter = "-";
    } else if (milliseconds > 0) {
        leadingCharacter = "+";
    } // else leadingCharacter = "";

    let seconds = Math.floor(milliseconds / 1000);
    let minutes = Math.floor(seconds / 60);

    seconds -= minutes * 60;
    seconds = String(seconds).padStart(2, "0");

    return `${leadingCharacter}${minutes}:${seconds}`;
}

function matchPass(journey, location) {
    if (journey === null) {
        return null;
    }

    let passes = journey.passes.map(pass => {
        pass.directDistance = getDirectDistance(pass.location, location);
        return pass;
    })
    .sort((a, b) => a.directDistance - b.directDistance);

    return passes[0];
}

// Events
onMount(() => {
    journeyPoller = createPoller({
        query: `
            query($token: String!) {
                liveBusJourney(busJourneyId: $token) {
                    shouldPoll
                    refreshId
                    pollAfter
                    journey {
                        id
                        name
                        currentPass {
                            stopId
                            name
                            status
                            arrivalDelta {
                                onTime
                                milliseconds
                            }
                        }
                        passes {
                            stopId
                            name
                            status
                            platform
                            location {
                                lat
                                long
                            }
                            expectedArrivalTime {
                                timestamp
                            }
                        }
                        line {
                            destination {
                                id
                                name
                            }
                        }
                    }
                }
            }
        `,
        initialToken: step.refreshId,
        key: "liveBusJourney",
        id: step.randomId,
        memoizationTags: ["liveBusJourney", "liveRouteJourney"],
        onData(data, instance) {
            if (!!data?.journey) {
                journey = data.journey;
                currentPass = journey.currentPass;
            }
        },
    });
});

onDestroy(() => {
    if (journeyPoller) journeyPoller.stop();
    if (locationPoller) locationPoller.stop();
});
</script>

<style lang="scss">

span.delta {
    padding: 2px 4px;

    background-color: #4c6dff;
    color: #ffffff;
    border-radius: 5px;
}

.platform {
    font-size: 0.8em;
    font-weight: 600;
    color: var(--lowContrastColor);
}

.differentTime {
    font-weight: 600;
    color: var(--errorColor);
}

.originalTimeDifferent {
    font-size: 0.7em;
    text-decoration: line-through;
}

</style>